// Base
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
}

body {
  padding: 0;  
  margin: 0;
  color: var(--base-txt-color);
  font-family: var(--base-font-family);
  font-weight: 300;
  background: linear-gradient(49.11deg, #00A0EE -12.63%, #A6D7EF 53.47%);  
  height: 100vh;
}

body.auth-bg {
  background: url('../../static/images/background.jpg') no-repeat center center fixed;
  background-size: cover;
  background-color: #fff; /* fallback color */
}

// background birds
.background-bird {
  position: absolute;
  z-index: -10;
  opacity: 90%;


}

  .big-bird {
    height: 40em;
  }

  .small-bird {
    height: 20em;
  }

.bird-flipped {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}



.bird-btm-left{
    bottom: 5em;
    left: 1em;
}

.bird-top-left{
    top: 5em;
    left: 1em;
}

.bird-btm-right{
    bottom: 5em;
    right: 1em;
}

.bird-top-right{
    top: 5em;
    right: 1em;
}


// headings ------------
h1,h2,h3,h5,h6 {
    font-family: Yomogi; 
    margin: 0 0;
    color: var(--heading-color);
}

h2 {
  font-family: Yomogi; 
  color : var(--re-orange);
}



// links ---------------
a {
  color: var(--link-color);
}


// app splash screen -------
.app-splash {
  position: fixed;
  background: #fff;
  color: #777;
  display: flex;
  left: 0;
  top:0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  text-align: center;
  .app-logo {
    height: 5em;
    display: block;
    margin-bottom: 1em;
  }
}


// helper classes ---------
.calign {
  text-align: center;
}

.large-text {
  font-size: 1.6em;
}

.brand-color {
  color: var(--brand-color);
}

.column-align {
  display: flex;
  flex-direction: column;
  align-items: center;
}

// generic-entry ------------
.generic-entry {
  box-shadow: 0px 3px 5px rgba(0,0,0,0.07);
  border-radius: 5px;
  margin-bottom: .5em;
  padding: 1em;
  background: #fff;
}

// root ---------------
#root {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;  
  overflow: hidden;
}

// page --------------------
.page-content {
  position: absolute;
  top: var(--app-header-height);
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  padding: 1em;
  &.no-pad {
    padding: 0;
  }

  display:flex;
  justify-content: center;
}

.page-inner-content {
  width:100%;
  max-width: 1024px;
  height: fit-content;
}

.page-centered {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-title{
  color: var(--re-orange);
  margin-bottom: 0.5em;
}

// sign in + sign up pages ----------
.signinup-box,
.signup-box {
  text-align: center;
  background: var( --re-light-green-bg);
  padding: 3em;
  border-radius: 1em;
  border: 1px solid rgba(255, 255, 255, 0.63);

  h4 {
    padding: 0;
    margin: 0 0 0.5em 0;
  }

}

.signinup-box h1{
  font-size: 1.5em;
  margin-bottom: 0.5em;
}

.signinup-logo {
  width: 100%;
}

.orange-input {
  background: var(--re-orange);
  border-radius: 10px;
  padding: 1em 0.5em 0.2em 0.5em;
  margin-bottom: 1em;;
}


// home page -------------------
.home-section {
  background: #fff;
  border-radius: 1em;
  padding: 0.5em;
  align-self: start;

  sl-icon {
    margin-right: 0.5em;;
  }

  sl-button{
    width: 50%;
  }

  h2 {
      display: flex;
    align-items: center;
  }
}

.section-middle {
  padding: 0.5em;
    min-height: 60%;

    sl-avatar{
      --size: 3em;
    }
}

.section-bottom{ 
    margin-top: 0.5em; 
    width: 100%;
    display: flex;
    justify-content: center;
  }



.home-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr auto auto;
  gap: 1em;
  margin-bottom: 10em;
  
}
@media all and (max-width: 768px){

  .home-grid{
      grid-template-columns: 1fr;
      grid-template-rows: auto auto auto auto;    
  }
}

.family {
  align-self: stretch;
  border: 2px solid var(--re-light-green);
  h2{
    color: var(--re-light-green);
  }

  h3 {
    margin-bottom: 1em;;
  }

  sl-divider {
    --color : var(--re-light-green);
    margin-bottom: 0;
  }

  .member-list{
    display: flex;
    gap: 0.5em;
    flex-wrap: wrap;
    justify-content: center;
  }
}


.stats {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;



  p {
    margin: 0.5em;;
  }
}


.task-roundup {
  border: 2px solid var(--re-orange);


  h2 {
    color: var(--re-orange);

  }

    sl-divider {
    --color : var(--re-orange);
    margin-bottom: 0;
  }

}



.task {
  border: 2px solid var(--re-bluegreen);
  grid-column: 1 / -1;

  h2 {    
    color: var(--re-bluegreen);

  }

   sl-divider {
    --color : var(--re-bluegreen);
    margin-bottom: 1em;
  }

  .tasks-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1em;
    flex-wrap: wrap;
    width: 100%;
    align-items: stretch;

  }


}



// Family page --------------
.family-container {
  width: 100%;
  background: #fff;
  min-height: 60%;
  border-radius: 1em;
  border: solid 2px var(--re-light-green);

  padding : 2em;

  display: flex;
  flex-direction:column;
}

.member-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1em;
  margin: 1em 0;
  flex-grow: 1;
  
  sl-card {
    border: 1px solid var(--re-light-green);
    height: auto;
    align-self: start;
  }
}

@media all and (max-width: 1000px){
  .member-container{
    grid-template-columns: 1fr 1fr;  
  }
}

@media all and (max-width: 700px){
  .member-container{
    grid-template-columns: 1fr;  
  }
}

.member-card-top{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--re-dark-green);

  p, h4 {
    padding: 0;
    margin: 0;
  }
}

.family-container-bottom {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: auto;
  gap: 1em;
}

.green-bg{
  background : linear-gradient(45deg, var(--re-light-green),var(--sl-color-success-300));
}




// Profile page ----------------
.profile-container {
  width: 100%;
  background: #fff;
  min-height: 60%;
  border-radius: 1em;
  border: solid 2px var(--re-bluegreen);
  padding: 2em;
}


.profile-container-upper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1em;;

  h2 {
    font-size: 3em;
    color: var(--re-bluegreen);
  }
}

.profile-details{
  margin: 1em 0px;
  padding: 1em;


  form {
    margin-left: auto;
    margin-right: auto;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    gap: 1em;
    justify-content: center;

  }

  .update-btn {
    align-self: center;
  }
}


@media all and (max-width: 768px){

  .profile-container-upper {
    flex-direction: column-reverse
  }
}





// guide page ---------------
.guide-step {
  padding: 1em;
  margin: 1.5em auto;
  max-width: 600px;
  img {
    width: 100%;
    height: auto;
  }
}

.guide-bottom {
  display: flex;
  justify-content:flex-end;
  align-items: center;
  gap: 1em;
}



.guide-container {
  background: #ffffffc2;
  min-height: 90%;
  border-radius: 1em;
  border: 2px solid var(--re-dark-green);
  padding: 2em;
}

// Manage Task Page--------------------
.manage-task-container {
  background: #fff;
  border-radius: 1em;
  border: 2px solid var(--re-orange);
  padding: 2em;

  .tasks-grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1em;
  }
}

.add-task-container {
  margin-bottom: 2em;
}





// Edit Task page --------------------
.edit-task-container {
  background: #fff;
  border-radius: 1em;
  border: 2px solid var(--re-orange);
  padding: 2em;

  sl-input, sl-textarea, sl-select {
    margin-bottom: 1em;
  }

  form {
    margin-right: auto;
    margin-left: auto;

    
  }


}



// RESPONSIVE - MOBILE -------------------
@media all and (max-width: 500px){ 

  .signinup-box {
    padding: 1em;
    width: 95%;
    max-width: none;
  }
}